define('megd/tests/utils/pricing-search', ['exports'], function (exports) {
  'use strict';

  exports['default'] = {
    fields: ['name'],
    filter: function filter(doc) {
      var uidx = doc._id.indexOf('_'),
          doctype = doc._id.substring(0, uidx);
      return doctype === 'pricing';
    }
  };
});